function readBlobAsBase64(blob) {
  const reader = new FileReader();
  reader.readAsDataURL(blob);
  return new Promise((resolve, reject) => {
    reader.onload = () => resolve(reader.result);
    reader.onerror = (error) => reject(error);
  });
}

function compressImage(imageBase64, quality) {
  const blob = base64ToBlob(imageBase64.split(',')[1], 'image/jpeg');
  const compressor = new Compressor(blob, {
    quality: quality,
    success(result) {
      // The result is a compressed Blob
      // Convert it back to base64
      readBlobAsBase64(result).then((compressedBase64) => {
      
		SendToOCRSpace(compressedBase64);
      });
    },
    error(err) {
      console.log(err.message);
    }
  });
}


function base64ToBlob(base64, contentType) {
  contentType = contentType || '';
  const sliceSize = 512;
  const byteCharacters = atob(base64);
  const bytesLength = byteCharacters.length;
  const slicesCount = Math.ceil(bytesLength / sliceSize);
  const byteArrays = new Array(slicesCount);

  for (let sliceIndex = 0; sliceIndex < slicesCount; ++sliceIndex) {
    const begin = sliceIndex * sliceSize;
    const end = Math.min(begin + sliceSize, bytesLength);

    const bytes = new Array(end - begin);
    for (let offset = begin, i = 0; offset < end; ++i, ++offset) {
      bytes[i] = byteCharacters[offset].charCodeAt(0);
    }
    byteArrays[sliceIndex] = new Uint8Array(bytes);
  }
  return new Blob(byteArrays, { type: contentType });
}
	
