// src/App.js

import React, { useState } from 'react';
import { Container, Row, Col, Form, Button } from 'reactstrap';
import TheForm from './form';
const Login = ({ onLogin }) => {
  //const [email, setEmail] = useState('');
  //const [password, setPassword] = useState('');
  
  const handleDownload = () => {
    // Remplacez 'path_to_your_pdf.pdf' par le chemin relatif ou absolu vers votre fichier PDF
    const pdfUrl = 'assets/modele.pdf'; // Par exemple, si votre fichier est dans public/assets

    // Crée un élément <a> temporaire et simule un clic pour déclencher le téléchargement
    const link = document.createElement('a');
    link.href = pdfUrl;
    link.download = 'modele.pdf'; // Vous pouvez spécifier un nom de fichier pour le téléchargement

    // Définit la propriété 'click()' pour simuler un clic sur le lien
    link.click();
  };
  
  
  const handleLogin = (e) => {
    e.preventDefault();
	localStorage.setItem('userSuccessLogin', 'true');
      onLogin(true);
    // if (email === 'genletter' && password === 'admin') {
      // localStorage.setItem('userSuccessLogin', 'true');
      // onLogin(true);
    // } else {
      // alert('Invalid login credentials');
    // }
  };

  return (
    <Container className="d-flex flex-column align-items-center justify-content-center min-vh-90">
      <Row className="w-100">
        <Col md={{ size: 6, offset: 3 }} lg={{ size: 4, offset: 4 }}>
          <div className="p-4 border rounded bg-white shadow-sm">
            <h2 className="text-center mb-4">Créez des lettres de motivation gratuitement</h2>
            <Form onSubmit={handleLogin}>
				{/*<FormGroup>
                <Label for="email">Identifiant</Label>
                <Input
                  type="text"
                  name="email"
                  id="email"
                  placeholder="Entrer votre identifiant"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  required
                />
              </FormGroup>
              <FormGroup>
                <Label for="password">Mot de passe</Label>
                <Input
                  type="password"
                  name="password"
                  id="password"
                  placeholder="Entrer le mot de passe"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  required
                />
				</FormGroup> */}
              <Button color="primary" block>Ouvrir</Button>
            </Form>
          </div>
        </Col>
      </Row>
	  <Button variant="primary" onClick={handleDownload} style={{ marginTop: '20px' }}>
          Télécharger un modèle
        </Button>
      <footer className="mt-4 text-center">
        <p>Application développée par l'équipe de <a href="https://corrige-mon-texte.org">corrige-mon-texte.org</a>, visant à aider les utilisateurs dans la génération automatique de lettres de motivation signées et datées.</p>
        <p>Pour toute assistance, veuillez appeler le : +243975440756</p>
      </footer>
    </Container>
  );
};

const App = () => {
  const [isAuthenticated, setIsAuthenticated] = useState(false);

  // useEffect(() => {
    // const userSuccessLogin = localStorage.getItem('userSuccessLogin');
    // if (userSuccessLogin === 'true') {
      // setIsAuthenticated(true);
    // }
  // }, []);

  const handleLogin = (status) => {
    setIsAuthenticated(status);
  };

  if (isAuthenticated) {
    return <TheForm />;
  }

  return <Login onLogin={handleLogin} />;
};

export default App;
